.rightbar
    grid-area: rightbar
    background-color: rgb(223, 222, 221)
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center
    margin: 0 20px
    max-height: 82vh
    box-sizing: border-box
    position: relative
    z-index: 2
    max-width: 25vw
    overflow-x: hidden

    &__picdiv
        width: 23vh
        height: 23vh
        position: relative
        margin: 20px 0

        &__txt
            position: absolute
            box-sizing: border-box
            top: 0
            left: 0
            width: 100%
            padding: 5px
            z-index: 2
            text-align: center
            color: white
            background-color: rgba(0, 0, 0, 0.5)
            transition: .3s

        img
            height: 23vmin
            width: 23vmin
            object-fit: cover

            &:hover
                cursor: pointer

            &:hover + .rightbar__picdiv__txt
                cursor: pointer
                background-color: rgb(0, 0, 0)

@media (orientation: portrait)
    .rightbar
        margin: 0

        &__picdiv
            height: 23vmin
            width: 23vmin
            object-fit: cover

            &__txt
                width: 23vw

    @media (max-width: 1000px)
        .rightbar
            &__picdiv
                &__txt
                    width: 100%

@media (max-width: 1000px)
    .rightbar
        max-width: 50vw
        justify-content: space-evenly
        transform: translateY(-10%)
        
        &__picdiv
            height: auto
            width: 40vw
            cursor: pointer

            &__txt
                padding: 2px
                font-size: 16px

            img
                display: none